body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background: lightsteelblue;
  font-family: "Merriweather", serif;
  font-size: 20px;
}

.container {
  background: #85dcbe;
  margin: 20px 270px 20px;
  padding-bottom: 200px;
  border: darkslateblue solid 15px;
  border-radius: 15px;
  text-align: center;
}

h1 {
  text-align: center;
  margin-top: 50px;
  color: #ee4c7c;
  font-size: 40px;
}

.card-header1 {
  color: #2f2fa2;
  font-size: 24px;
}

.card-header2 {
  margin-top: 6px;
  color: darkslategray;
  font-size: 15px;
}

li {
  list-style: none;
  margin-left: 15px;
  text-align: center;
}

button {
  margin-left: 2px;
  margin-bottom: 2px;
}

button .btn btn-danger {
  margin-left: 10px;
  margin-bottom: 2px;
}

.fas fa-plus-square {
  margin-left: 5px;
}

input[type="text"] {
  padding: 4px;
  height: 37px;
  width: 400px;
}

::placeholder {
  color: lightblue;
  opacity: 1;
  padding-left: 5px;
}
